<template>
  <div>
    <h1>اطلاعات کاربری</h1>
    <div>
      <v-row class="mt-6">
        <v-col cols="12" sm="12" md="4">
          <v-text-field
            label="نام"
            v-model="profile.name"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="4">
          <v-text-field
            label="نام خانوادگی"
            v-model="profile.lastName"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="4">
          <v-text-field
            label="ایمیل"
            v-model="profile.email"
            solo
            :disabled="!isFirstTime"
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="12">
          <v-text-field
            label="آدرس"
            v-model="profile.address"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-btn
          color="primary"
          class="ml-4"
          width="150"
          @click="postProfile()"
          :loading="loading"
          >ویرایش</v-btn
        >
        <v-btn color="secondary" outlined width="150" @click="$router.go(-1)">برگشت</v-btn>
      </v-row>
    </div>
  </div>
</template>
   
   <script>
import axios from "axios";
export default {
  name: "Profile",
  data() {
    return {
      loading: false,
      isFirstTime: true,
      profile: {
        email: "",
        address: "",
        name: "",
        lastName: "",
      },
    };
  },
  mounted() {
    this.getProfile();
  },
  computed: {
    userID() {
      return this.$route.params.id;
    },
  },
  methods: {
    async getProfile() {
      this.loading = true;
      try {
        const { data } = await axios({
          method: "GET",
          url: "/profile/admin/" + this.userID,
          headers: {
            Authorization: "Bearer " + this.$cookies.get("admin-token"),
          },
        });
        this.profile = data;
        this.isFirstTime = false;
      } catch {
        this.$toast.error("اطلاعات حساب کابری یافت نشد");
      } finally {
        this.loading = false;
      }
    },
    async postProfile() {
      const dataToSend = {
        ...this.profile,
      };

      if (dataToSend.lastName.length < 3 || dataToSend.name.length < 3) {
        this.$toast.error("نام  و نام خانوادگی باید بیشتر از 3 کاراکتر باشد");
        return;
      }

      const reg = new RegExp(
        /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
      );
      if (!reg.test("" + dataToSend.email)) {
        this.$toast.error("ایمیل معتبر نمی باشد");
        return;
      }

      if (dataToSend.address.length < 8) {
        this.$toast.error("  آدرس باید بیشتر از 8 کاراکتر باشد");
        return;
      }

      this.loading = true;

      //   if (!this.isFirstTime) delete dataToSend.email;
      try {
        await axios({
          method: this.isFirstTime ? "POST" : "PATCH",
          url: "/profile/admin/" + this.userID,
          headers: {
            Authorization: "Bearer " + this.$cookies.get("admin-token"),
          },
          data: dataToSend,
        });

        if (this.isFirstTime) {
          this.isFirstTime = false;
        }

        this.$toast.success('اطلاعات پروفایل ویرایش شد')

      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
    async putProfile() {
      this.loading = true;
      try {
        await axios({
          method: "PATCH",
          url: "/profile/admin/" + this.userID,
          headers: {
            Authorization: "Bearer " + this.$cookies.get("admin-token"),
          },
          data: this.profile,
        });
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>
   
